<template>
  <div id="app">
    <el-backtop :bottom="miniMode ? 88 : 40" :right="miniMode ? 20 : 40">↑</el-backtop>
    <Nav/>
    <div class="body">
      <router-view/>
    </div>
    <Footer/>
    <Tabbar/>
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from '@/components/Footer.vue'
import Nav from '@/components/Nav.vue'
import Tabbar from '@/components/Tabbar.vue'

export default {
  name: 'Home',
  watch:{
    '$route.path':function(){
      this.$store.commit('closeMenu')
      let title = this.$store.getters['siteTitle']
      document.title = this.$route.meta.title + ' - ' + title
    }
  },
  computed: {
    // 计算属性的 getter
    miniMode: function () {
      // `this` 指向 vm 实例
      return this.$store.getters['miniMode']
    }
  },
  components: {
    Nav,
    Footer,
    Tabbar,
  },
  mounted () {
    const that = this
    that.$store.commit('setWindow',window)
    window.onresize = () => {
      return (() => {
        that.$store.commit('setWindow',window)
      })()
    }
  }
}
</script>

