import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//element-ui
import './plugs/element/import-element'


Vue.config.productionTip = false

//路由跳转后回到页面顶部
router.beforeEach((a,b,c)=>{
  c()
  window.scrollTo(0,0)
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
