import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  site:{
    title:'郎溪立能通风设备有限公司'
  },
  //迷你模式菜单是否打开
  menuIsOpen:false,
  //记录当前窗口大小
  window:{
    width:0,
    height:0
  }
}
const getters = {
  siteTitle(){
    return state.site.title
  },
  //是否为mini模式
  miniMode(){
    return state.window.width < 768
  },
  //返回迷你模式菜单是否打开
  menuIsOpen(){
    return state.menuIsOpen
  },
  windowWith(){
    return state.window.width
  },
  windowHeight(){
    return state.window.height
  },
  welcomeHeight(){
    return state.window.height - 88
  }
}
const mutations = {
  setSiteTitle(state,value){
    state.site.title = value
  },
  clickMenuButton(state){
    state.menuIsOpen = !state.menuIsOpen
  },
  openMenu(state){
    state.menuIsOpen = true
  },
  closeMenu(state){
    state.menuIsOpen = false
  },
  setWindow(state,obj){
    state.window.width = obj.innerWidth
    state.window.height = obj.innerHeight
  }
}
const actions = {

}
export default new Vuex.Store({
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions,
  modules: {
  }
})
