<template>
    <div style="height:88px;">
        <div class="nav">
            <div class="nav-mini-button" :class="miniMode ? '' : 'hidden'" @click="handleOpen">
                <p><i :class="menuIsOpen == false ? 'el-icon-menu' : 'el-icon-circle-close'"></i></p>
            </div>
            <div class="logo">
                <router-link to="/"><img src="https://cdn.jsdelivr.net/gh/q240980/web/lntfsb.com/image/logo.png"></router-link>
            </div>
            <div :class="miniMode && !menuIsOpen  ? 'hidden' : ''">
                <div :class="miniMode ? 'nav-menu-down' : 'nav-menu'">
                    <template  v-for="(item, index) in $router.options.routes">
                        <router-link
                            v-if="!item.hide"
                            :to="{path: item.path}" 
                            :key="index"
                        >
                            {{item.meta.title}}
                        </router-link>
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Nav',
  computed: {
    // 计算属性的 getter
    miniMode: function () {
      // `this` 指向 vm 实例
      return this.$store.getters['miniMode']
    },
    menuIsOpen: function () {
      return this.$store.getters['menuIsOpen']
    }
  },
    methods: {
        handleOpen(){
            this.$store.commit('clickMenuButton')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav {
    position:fixed;
    top:0;
    left:0;
    right:0;
    height:88px;
    z-index:100;
    color:red;
    background-color: rgba(0,0,0,.8);
}
a{
    color:#fff;
}
.logo{
    cursor: pointer;
    line-height:88px;
    display:flex;
    float:left;
    margin-left:10px;
}
.logo img{
    vertical-align: middle;
    height:36px;
    width:auto;
}
.nav-menu{
    position:fixed;
    top:0;
    right:0;
    line-height:48px;
    margin-top: 20px;
}
.nav-menu a{
    transition:all 0.5s; 
    background-color: none;
    color:#fff;
    display: inline-block;
    font-size:18px;
    padding:0 20px;
    border-bottom: 2px solid rgba(255,0,0,0);
}
.nav-menu a:hover{
    color:red;
    border-bottom: 2px solid red;
}
.nav-menu-down{
    position:fixed;
    width:100%;
    top: 88px;
    left: 0;
    background-color: rgba(0,0,0,.6);
    width: 100%;
    z-index:100;
}
.nav-menu-down a{
    display:block;
    height:36px;
    
}
a.router-link-exact-active {
  color: red;
}
.nav-mini-button{
    cursor: pointer;
    width:88px;
    line-height:88px;
    display:flex;
    float:right;
}
.nav-mini-button p{
    margin:0 auto;
    font-size:36px;
}
.el-menu.el-menu--horizontal{
    border: none;
    font-size: 140px;
}
</style>
